.user-container{
    padding: 10px;
    margin: 10px;
}

.whiteBackground {
    background-color: white;
}

.orangeBackground {
    background-color: orange;
}


.greenBorder{
    border : 1px solid green;
    box-shadow: 3px 3px green;
}

.redBorder{
    border : 1px solid red;
    box-shadow: 3px 3px red;
}

.address-container{
    -webkit-border-radius: 20px;
    border-radius: 20px;
    background-color: #669999;
    border: 1px black solid;
    margin: 10px;
    padding: 10px;
}

.showElement
{
    /* visibility: visible; */
    display: show;
}

.hideElement{
    /* visibility: hidden; */
    display: none;
}

.address-container input{
    background-color: #669999;
}


.otherDataStyle{
    background-color: grey;
    width: 90px;
}


.otherDataStyle:hover{
    color: white;
}

.update-delete-container{
    display: flex;
    flex-direction: row-reverse
}

.update-delete-item{
    margin: 2px; 
}

.greenLookLikeLink{
    cursor:pointer;
    color:green;
    text-decoration:underline;
}

.redLookLikeLink{
    cursor:pointer;
    color:red;
    text-decoration:underline;
}
